
export default (state,{type,payload})=>{
    switch(type){
        case 'VIEW_LOADING':
            return {...state,Loading:payload}
        case 'UPDATE_HOME':
            
             return {...state,home:payload}
        case 'CLEAR_HOME':
                console.log(111)
            return {...state,home:payload}
        case 'CLEAR_BANNER':
                
            return {...state,banner:payload}
        case 'UPDATE_BANNER':
                
             return {...state,banner:payload}
        case 'CLEAR_LIST':
                console.log(33)
            return {...state,list:payload}
        case 'UPDATE_LIST':
                
             return {...state,list:payload}
        case 'CHECK_USER':
            return {...state,user:payload}
        case 'CLEAR_USER':
            return {...state,user:{
                err:1,
                msg:'未登录',
                data:{}
            }}
        default:
            return state;
    }
}