import React from 'react';
import '../assets/css/Footer.css'

import { Row, Col, Icon  } from 'antd';
import { NavLink } from 'react-router-dom';

export default class Footer extends React.Component{
    render(){
        return (
            <div className="Footer">

                <Row>
                    <Col span={8}><NavLink to="/home"><Icon className="icon" type="home" style={{ fontSize: '35px' }} /><span>首页</span> </NavLink></Col>
                    <Col span={8}><NavLink to="/list"><Icon className="icon" type="sound" style={{ fontSize: '35px' }} /><span>热文</span></NavLink></Col>
                    <Col span={8}><NavLink to="/user"><Icon className="icon" type="user" style={{ fontSize: '35px' }} /><span>用户</span> </NavLink></Col>
                </Row>
            </div>
        )
    }
}