import axios from 'axios';
import { CHECK_USER } from './types'
//id不是必传参数，所有给个默认值为空
const update = ({api,_page=1,_limit=8,id=null,type}) => (dispatch) => {
    axios({
        url: id ? `/api/${api}/${id}` : `/api/${api}`,
        //params:{_page,_limit}
    }).then(
        res=>{
            console.log(res.data.data,type)
            dispatch({type:type,payload:res.data.data})
        }
    ).catch(
        err=>new Error(err)
    )
}

const checkUser = ({api,method='get',name,password}) => (dispatch,getState) => (
    axios({
      url:`/api/${api}`,
      method,
      params: method === 'get' ? {name, password}: null,
      data: method === 'post' ? {name, password}: null,
    }).then(
      res=>{
          console.log(res.data);
        dispatch({type:CHECK_USER,payload:res.data});
        return res.data
      }
    )
  );

export {update,checkUser}