import React from 'react';
import style from  '../assets/css/User.module.css'


import { Avatar, Row, Col, Icon } from 'antd';
import Footer from '../component/Footer';

import store from '../plugins/redux';
export default class User extends React.Component{
    render(){
        return (
            <div className="User">
                <div className={style.head}>
                    {/* <Avatar className={style.user} size={85} icon="user" /> */}
                    <span onClick={this.logout} className={style.logout}>注销</span>
                    <img src={`${React.baseUrl}`+this.props.data.icon} className={style.user}></img>
                    <Row className={style.row}>
                        <Col span={12}><Icon className={style.icon} style={{ fontSize: '25px' }} type="heart" /><span>{this.props.data.follow}</span></Col>
                        <Col span={12}><Icon className={style.icon} style={{ fontSize: '25px' }} type="team" /><span>{this.props.data.follow}</span></Col>
                    </Row>
                </div>
                <Footer></Footer>
            </div>
        )
    }
    logout = () => {
        React.axios({
            url:'/api/logout',
            method:'delete'
        }).then(
            res=>{
                if(res.data.err === 0){
                    //清除localStorage和状态管理
                    window.localStorage.removeItem('react_news_app_uid');
                    store.dispatch({type:'CLEAR_USER'})
                }
            }
        )
    }
}