import React from 'react';

import { NavLink } from 'react-router-dom';
import { withRouter } from 'react-router-dom'
import { List, Avatar } from 'antd';
class Cell extends React.Component{
    componentDidMount(){
        console.log(this.props.dataname);
    }
    render(){
        return (
            <div className="Cell">
                        <List
                        dataname={this.props.dataName}
                        itemLayout="horizontal"
                        dataSource={this.props.data}
                        renderItem={item => (
                          
                        <List.Item onClick={()=>{this.detail(item._id,this.props.dataname)}}>
                            <List.Item.Meta
                            // avatar={<Avatar src={item.detail.auth_icon} />}
                            title={<a>{item.title}</a>}
                            description={item.des}
                            />
                        </List.Item>
                        
                        )}
                    />
            </div>
        )
    }
    detail=(id,dataName)=>{
      this.props.history.push({pathname:'/detail/'+ id,search:'dataName='+dataName})
      
    }
}
export default withRouter(Cell)