import React from 'react';
import '../assets/css/Reg.css'

import { Input, Tooltip, Icon, Button } from 'antd';

export default class Reg extends React.Component{
    state={
        username:'',
        password:'',
        mess:''
    }
    render(){
        return (
            <div>
                <div className="top">
                    <Icon  onClick={this.back} className="icon" type="left" style={{ fontSize: '30px' }}/>
                </div>
            <div className="Reg">
                <div className="icon">
                    <Icon type="user-add" style={{ fontSize: '150px' }} />
                </div>

                <div>
                <Input
                    onChange={this.username}
                    value={this.state.username}
                    className="input"
                    placeholder="输入账号"
                    prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    suffix={
                        <Tooltip title="Extra information">
                        <Icon type="info-circle" style={{ color: 'rgba(0,0,0,.45)' }} />
                        </Tooltip>
                    }
                    />
                <Input
                    onChange={this.password}
                    value={this.state.password}
                    className="input"
                    placeholder="输入密码"
                    prefix={<Icon type="menu" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    suffix={
                        <Tooltip title="Extra information">
                        <Icon type="info-circle" style={{ color: 'rgba(0,0,0,.45)' }} />
                        </Tooltip>
                    }
                />
                <span className="mess">{this.state.mess}</span>
                 <Button className="btn" type="primary" onClick={this.reg} block>注册</Button>
                </div>
            </div>
            </div>
        )
    }
    back=()=>{
        this.props.history.go(-1)
    }
    username=(ev)=>{
        console.log(ev.target.value)
        this.setState({
            username:ev.target.value
        })
    }
    password=(ev)=>{
        console.log(ev.target.value)
        this.setState({
            password:ev.target.value
        })
    }
    reg=()=>{
        React.axios({
            url:'/api/reg',
            method: 'post',
            data:{
                name:this.state.username,
                password:this.state.password
            }
        }).then(
            res=>{
                if(res.data.err === 1){
                    this.setState({mess:res.data.msg})
                }else{
                    
                    this.props.history.push('/login');
                }
            }
        )
    }
}