import React from 'react';
import '../assets/css/Detail.css'

import querystring from 'query-string'
import { Card, Icon } from 'antd';
const { Meta } = Card;


export default class Detail extends React.Component{
    state={
        detail:[]
    }
    async componentDidMount(){
        let id = this.props.match.params.id;
        let dataname = querystring.parse(this.props.location.search).dataName;
        console.log(id,dataname);
        await React.axios({
            url:'/api/'+ dataname +"/"+ id
        }).then(
            res=>{
                console.log(res.data.data)
                this.setState({
                detail:res.data.data
            })
        }
        )
    }
    render(){
        //console.log(this.state.detail.length)
        let el = null;
        if(this.state.detail.length>0){
            el = (
                <div className="Detail">
                <div className="top">
                    <Icon onClick={this.back} className="icon" type="left" style={{ fontSize: '30px' }}/>
                </div>
                <div>
                    <div className="up">
                        <h3>{this.state.detail[0].title}</h3>
                        <div className="auth">
                            <img className="img" src={React.baseUrl + this.state.detail[0].auth_icon} />
                            <span className="span">{this.state.detail[0].title}</span>
                        </div>
                        <p>{this.state.detail[0].content2}{this.state.detail[0].content}</p>
                        
                    </div>
                    <div>

                    </div>
                </div>
            </div>
            )
        }else{
            el = (
                <div>骨架屏</div>
            );
        }
        return el
    }
    back=()=>{
        this.props.history.go(-1)
    }
}