import React from 'react';
import '../assets/css/Login.css'

import { Input, Tooltip, Icon, Button } from 'antd';
import {checkUser} from '../store/actions'
import store from '../plugins/redux';
export default class Login extends React.Component{
    state={
        username:'',
        password:'',
        mess:""
    }
    render(){
        return (
            <div>
                <div className="top">
                    <Icon onClick={this.back} className="icon" type="left" style={{ fontSize: '30px' }}/>
                    <span className="reg" onClick={this.reg}>注册</span>
                </div>
            <div className="Login">
                <div className="icon">
                    <Icon type="team" style={{ fontSize: '150px' }} />
                </div>

                <div>
                <Input
                    onChange={this.name}
                    value={this.state.username}
                    className="input"
                    placeholder="输入账号"
                    prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    suffix={
                        <Tooltip title="Extra information">
                        <Icon type="info-circle" style={{ color: 'rgba(0,0,0,.45)' }} />
                        </Tooltip>
                    }
                    />
                <Input
                    onChange={this.password}
                    value={this.state.password}
                    className="input"
                    placeholder="输入密码"
                    prefix={<Icon type="menu" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    suffix={
                        <Tooltip title="Extra information">
                        <Icon type="info-circle" style={{ color: 'rgba(0,0,0,.45)' }} />
                        </Tooltip>
                    }
                />
                <span className="mess">{this.state.mess}</span>
                 <Button className="btn" type="primary" onClick={this.login} block>登陆</Button>
                </div>
            </div>
            </div>
        )
    }
    back=()=>{
        this.props.history.go(-1)
    }
    name=(ev)=>{
        console.log(ev.target.value)
        this.setState({
            username:ev.target.value
        })
    }
    password=(ev)=>{
        console.log(ev.target.value)
        this.setState({
            password:ev.target.value
        })
    }
    login=()=>{
        // console.log(this.state.username,this.state.password);
        // React.axios({
        //     url:'/mock/user',
        //     params:{
        //         username:this.state.username,
        //         password:this.state.password
        //     }
        // }).then(
        //     res=>{
        //         if(res.data.err === 1){
        //             this.setState({
        //                 mess:res.data.err
        //             })
        //         }else{
        //             alert("登陆成功");
        //             this.props.history.push('user')
        //         }
        //     }
        // )
        store.dispatch(checkUser({
            api:'login',
            method:'post',
            name:this.state.username,
            password:this.state.password
          })).then(
            data=>{
              if (data.err === 1){
                this.setState({mess:data.msg})
              } else {
                window.localStorage.setItem('react_news_app_uid',JSON.stringify(data));
                this.props.history.push('/user')
              }
      
            }
          )
    }
    reg=()=>{
        this.props.history.push("/reg");
    }
}