import React from 'react';
import '../assets/css/Home.css'

import Header from '../component/Header'
import Cell from '../component/Cell'
import Footer from '../component/Footer'

import {connect} from 'react-redux'
import {update} from '../store/actions'
import * as types from '../store/types'
class Home extends React.Component{

  constructor(props){
    super();
    props.getHome()
    props.getBanner()
    // React.axios({
    //     url:'/mock/home',
    //     params:{
    //         _page:1,
    //         _limit:5
    //     }
    // }).then(
    //     res=>this.setState({list:res.data.data})
    // )
    // React.axios({
    //     url:'/mock/banner',
    //     params:{
    //         _page:1,
    //         _limit:3
    //     }
    // }).then(
    //     res=>this.setState({banner:res.data.data})
    // )
}
    render(){
        return (
            <div className="Home">
                <Header banner={this.props.banner} dataname="banner"></Header>
                <Cell data={this.props.home} dataname="home"></Cell>
                <Footer></Footer>
            </div>
        )
    }
}



const mapState = state => ({
    banner:state.banner,
    home:state.home
})

const mapDispath = dispatch => ({
    getHome: ()=>{
        dispatch({type:'CLEAR_HOME'})
        dispatch(update({
            api:'home',
            type:types.UPDATE_HOME
        }))
        
    },
    getBanner: ()=>{
        // dispatch({type:'CLEAR_BANNER'})
        dispatch(update({
            api:'banner',
            type:types.UPDATE_BANNER
        }))
    },
})
export default connect(mapState,mapDispath)(Home)